import React, {DetailedHTMLProps, InputHTMLAttributes} from 'react'
import InputRange from "react-input-range";
import style from "../c8-SuperDoubleRange/style.module.css";

//    
type DefaultInputPropsType = DetailedHTMLProps<InputHTMLAttributes<HTMLInputElement>, HTMLInputElement>

//               
// (   value: string, onChange: ...;      DefaultInputPropsType)
type SuperRangePropsType = DefaultInputPropsType & { //  +       
    setState: (value: any) => void
    state: any
    step: number
};

const SuperRange: React.FC<SuperRangePropsType> = (
    {
        type,
        onChange,
        className,
        state, setState, step,
        ...restProps//       restProps
    }
) => {

    const setValueHandler = (value: any) => {
        if (value + step <= state.value5.max) {
            setState({value5: {min: value, max: state.value5.max}})
        }
    }
    return (
        <>
            <form className={style.form}>
                <InputRange
                    step={step}
                    maxValue={50}
                    minValue={0}
                    value={state.value5.min}
                    onChange={setValueHandler}
                />
            </form>
        </>
    )
}

export default SuperRange
